.no-suggestions {
  color: #999;
  padding: 0.5rem;
}
.suggestions {
  border: 1px solid #999;
  border-top-width: 0;
  list-style: none;
  margin-top: 0;
  max-height: 143px;
  overflow-y: auto;
  padding-left: 0;
  width: calc(300px + 1rem);
}
.suggestions li {
  padding: 0.5rem;
}
.suggestion-active,
.suggestions li:hover {
  background-color: #008f68;
  color: #fae042;
  cursor: pointer;
  font-weight: 700;
}
.suggestions li:not(:last-of-type) {
  border-bottom: 1px solid #999;
}
.recipe-card-link {
  text-decoration: none;
  color: black;
}
.item {
  display: flex;
  margin: 5px 0;
  background: rgba(238, 238, 238, 0.5);
}
.item img {
  width: 120px;
}
.item h4 {
  flex: 1 1 70%;
  margin: 0;
  font-family: 'Quicksand';
  font-size: 24px;
  text-align: left;
  padding: 5px;
  font-weight: 500;
}
